  font-weight:bolder;
}
.name-input{
  margin-bottom:10px;
}
.instructions,h1,h2,h3,p{
  padding-left:20px;
  padding-right:20px;
}
.meme-block.not-shown{
  display:none;
}
.delete-link{
  display:block;
  text-align:center;
  margin-top:10px;
  margin-bottom:-5px;
}

